@font-face {
  font-family: normalfont;
  src: url('./../fonts/notosans.ttf');
}
@font-face {
  font-family: latofont;
  src: url('./../fonts/Lato-Regular.ttf');
}
@font-face {
  font-family: codefont;
  src: url('./../fonts/FiraCode-VariableFont_wght.ttf');
}

code {
  /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
  font-family: codefont;
}

.App {
  margin: 0;
  padding: 0;

  height: fit-content;
  width: 80vw;

  padding-left: 10vw;
  padding-right: 10vw;

  overflow-y: hidden;
  overflow-x: hidden;

  font-size: 1rem;


  font-family: latofont;

  display: flex;
  flex-direction: column;
}
body::-webkit-scrollbar {
  display: none;
}
body {
  scroll-behavior: smooth;
}


.anchor {
  font-size: 2rem;
  opacity: 0.5;
  user-select: none;
  cursor: pointer;
  height: fit-content;
  width: fit-content;
  display: block;
  position: relative;
  left: -2rem;
  top: 4rem;
}

.topbarroot {
  display: block;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 10;
  height: fit-content;
}

.topbarinner {
  height: 1.6rem;
  padding: 0.2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* top: 0; */
}

.topbartitle {
  justify-self: center;
  justify-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  font-size: 1rem;
}

.border {
  border: 1px solid black;
}



.toggleroot {
  /* background-color: rgb(255, 255, 255); */
  background-color: transparent;
  height: fit-content;
  width: fit-content;
  border-radius: 0.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 0.2rem;
  justify-self: flex-end;
}



.toggle {
  user-select: none;
  pointer-events: none;
  height: 1rem;
  width: 1rem;
  border-radius: 0.5rem;
}



.rowroot {
  display: flex;
  flex-direction: row;
  
  justify-content: space-between;

  height: fit-content;
  padding-top: 1rem;
  padding-bottom: 1rem;

  min-height: 1rem;
}

.rvaligncenter {
  align-items: center;
}

.rvalignstart {
  align-items:flex-start;
}



p.text {
  margin: 0;
  padding: 0.5rem;
  flex-grow: 1;
  font-size: 1.1rem;
  line-height: 1.7rem;
  /* text-indent: 2rem; */
}

.justify {
  text-justify: inter-word;
}

.center {
  text-align: center;
}

.textleft {
  text-align: left;
}

.textright {
  text-align: right;
}




.diagramimg {
  display: block;
  object-fit: cover;
  border-radius: 1rem;
}


h1 {
  margin-top: 4rem;
  margin-bottom: 4rem;
  font-size: 4rem;
}

h2 {
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

h3 {
  margin-top: 0.7rem;
  margin-bottom: 0.7rem;
  font-size: 1.5rem;
}

a {
  /* all: unset; */
  text-align: center;
  cursor: pointer;
  font-style: underline;
  color: inherit;
}

.clippy {
  height: 1rem;
  margin-left: 0.2rem;
}

.clipboardbutton {
  cursor: pointer;
  float: right;
  opacity: 0.7;
  margin: 0.2rem;
  align-self: flex-end;
}